.beta-block__button {
  opacity: 1;
  margin-top: 4rem;
  background-color: rgb(241, 145, 1);
  color: #000000;
  max-width: 200px;
  max-height: 35px;
  font-size: 0.8rem;
}
.beta-block__button:hover {
  background-color: rgb(241, 145, 1);
}

.adventure {
  background-color: #0F141E;
  height: 300px;
}
@media screen and (min-width: 768px) {
  .adventure {
    height: 45vh;
  }
}
.adventure__container {
  position: relative;
  display: flex;
  flex-direction: row;
  height: 100%;
  padding: 0 0.5rem;
  overflow: hidden;
  font-family: "Noto Sans HK", sans-serif;
  color: white;
}
@media screen and (min-width: 768px) {
  .adventure__container {
    justify-content: space-between;
    padding: 0 2.5rem;
  }
}
.adventure__text {
  z-index: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-top: 2rem;
}
.adventure__title {
  margin: 0;
  line-height: 1;
  font-family: "Noto Sans HK", sans-serif;
  font-size: 2rem;
  color: #868281;
}
@media screen and (min-width: 768px) {
  .adventure__title {
    font-size: 5rem;
  }
}
.adventure__sub {
  margin: 0;
  line-height: 1;
  font-family: "Noto Sans HK", sans-serif;
  font-size: 3rem;
}
@media screen and (min-width: 768px) {
  .adventure__sub {
    font-size: 8rem;
  }
}
.adventure__part-underline {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 1rem 0 1rem;
  height: 4px;
  width: 125px;
  background-color: rgb(241, 145, 1);
}
.adventure__btn {
  max-height: 35px;
  max-width: 135px;
  margin-top: 1rem;
  font-size: 0.8rem;
}
@media screen and (min-width: 768px) {
  .adventure__btn {
    max-height: 55px;
    max-width: 185px;
    font-size: 1rem;
  }
}
.adventure img {
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  object-position: 3% 25%;
  width: 100%;
  height: 300px;
  object-fit: none;
}
@media screen and (min-width: 768px) {
  .adventure img {
    object-position: 100% 25%;
    height: 55vh;
  }
}

.newsblock {
  height: 100%;
  width: 100%;
  background-color: white;
}
@media screen and (min-width: 768px) {
  .newsblock {
    height: 80vh;
  }
}
.newsblock__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 0 1rem;
  padding-bottom: 2rem;
}
.newsblock__container .newsblock__title-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  position: relative;
}
.newsblock__container h1 {
  margin: 0;
  margin-bottom: 1rem;
  padding-top: 2rem;
  font-family: "Noto Sans HK", sans-serif;
  font-size: 2.775rem;
  line-height: 1;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .newsblock__container h1 {
    font-size: 8rem;
  }
}
.newsblock__title-animation {
  position: absolute;
  top: 15px;
  right: -75px;
  height: 150px;
  width: 50px;
  background-color: white;
  transform: rotate(25deg);
  z-index: 2;
}
.newsblock__part-underline {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  height: 4px;
  width: 65px;
  margin-left: 7px;
  margin-bottom: 2rem;
  background-color: rgb(30, 197, 227);
}
@media screen and (min-width: 768px) {
  .newsblock__part-underline {
    width: 125px;
    margin-bottom: 0;
  }
}
.newsblock__card-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .newsblock__card-wrapper {
    flex-direction: row;
  }
}
.newsblock__card {
  position: relative;
  max-width: 425px;
  width: 100%;
  height: 450px;
  margin: 0;
  margin-bottom: 4rem;
  padding: 0.5rem;
  padding-top: 0;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .newsblock__card {
    margin: 2rem;
    margin-top: 5rem;
  }
}
.newsblock__card-content {
  padding: 0 10px 0 0;
  font-family: "IBM Plex Serif", serif;
}
.newsblock .card__thumbnail {
  object-fit: cover;
  object-position: center 40%;
  height: 230px;
  width: 100%;
}
.newsblock .card__thumbnail-wrapper {
  overflow: hidden;
}
.newsblock .card__date {
  margin: 0;
  font-size: 0.75rem;
}
.newsblock .card__title {
  margin: 0.5rem 0;
  font-family: "Noto Sans HK", sans-serif;
}
.newsblock .card__paragraph {
  margin: 0;
  font-family: "Noto Sans HK", sans-serif;
  font-size: 0.875rem;
  line-height: 1.5;
}

.login-account {
  background: rgba(0, 0, 0, 0.39);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-height: 500px;
  height: 100%;
  width: 500px;
  color: white;
}
.login-account h5 {
  font-size: 3rem;
  margin: 0;
  margin-bottom: 1rem;
}
.login-account form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 300px;
  margin-bottom: 3.5rem;
  transition: none;
}
.login-account form .btn {
  transition: none;
}
.login-account form .btn:hover {
  transform: none;
}
.login-account__section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}
.login-account__section label {
  width: 100%;
  text-align: left;
}
.login-account__section input {
  width: 100%;
  height: 2rem;
}
.login-account__section button {
  margin-top: 1.5rem;
  font-size: 1rem;
  max-width: none;
  width: 100%;
  height: 2rem;
}
.login-account__register-link {
  color: #fff;
}

